@import 'carbon-components/scss/components/ui-shell/functions';
@import 'carbon-components/scss/components/ui-shell/theme';

@import 'variables';

login-button-widget {
  display: flex;
  align-items: center;
}

.LoginButtonWidget {
  &__wellcome {
    display: inline-block;
    color: var(--secondary-color, $secondary-color-01);
    padding: 0 16px;
    line-height: mini-units(6);
    height: mini-units(6);
    font-size: 14px;
    & > a {
      color: $shell-header-text-02;
    }
  }
}